#page {
   // Header
   header {
      padding: 30px 0;

      .container {
         display: flex;
         align-items: center;
         justify-content: space-between;
         gap: 20px;
      }

      .main-logo {
         font-weight: 700;
         font-size: 1.5rem;

         img {
            height: 35px;
         }
      }

      nav {

         ul {
            position: relative;
            transform: all 0.35s ease;

            li {
               border-bottom: 1px solid transparent;

               > a {

                  &:hover {
                     border-bottom: 1px solid var(--color-white);
                  }

                  &.active {
                     border-bottom: 1px solid white;
                  }
               }

               &:hover {
                  .submenu {
                     opacity: 1;
                     visibility: visible;
                  }
               }

               .submenu {
                  opacity: 0;
                  visibility: hidden;
                  position: absolute;
                  top: 100%;
                  flex-direction: column;
                  gap: 5px;
                  padding: 5px 10px;
                  background: #333;

                  li {
                     a {
                        &.active {
                           border-bottom: 1px solid white;
                        }
                     }
                  }
               }
            }
         }

         a {
            color: var(--color-white);
            text-decoration: none;
            transition: all 0.25s ease;

            &:hover {
               opacity: 0.5;
            }
         }
      }
   }

   // Main
   main {

      p {
         color: #AAA;
      }
   }

   // Contact
   .contact {
      
      main {
         display: flex;
         gap: 20px;
      }
   }
}